// @flow

import React, {useState, useEffect, useCallback} from 'react'
import {ThemeProvider} from 'styled-components'
import {CssBaseline} from '@material-ui/core'
import {createMuiTheme} from '@material-ui/core/styles'

import {GlobalStyle, Container, Counter, Overlay, Number} from './styles'

import {useInterval} from '../../../utils/common'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

const KegelTraining = () => {
  const [num, setNum] = useState<number>(100)

  const [counterNum, setCounterNum] = useState<number>(5)

  const [counting, setCounting] = useState<boolean>(false)

  const [counterDelay, setCounterDelay] = useState<?number>(null)

  const counterTimeout = useCallback(() => {
    setCounterNum(value => {
      const newNum: number = value - 1

      if (newNum === 0) {
        setCounting(false)
        setNum(n => n - 1)
        setCounterDelay(null)
      }

      return newNum
    })
  }, [])

  useInterval(counterTimeout, counterDelay)

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.keyCode === 32 && !counting && num > 0) {
        setCounting(true)
        setCounterNum(5)
        setCounterDelay(1000)
      }
    }

    document.addEventListener('keydown', handleKeyDown)
    return () => {
      document.removeEventListener('keydown', handleKeyDown)
    }
  }, [counting, num])

  return (
    <>
      <CssBaseline />
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        <Container>
          {counting ? <Counter>{counterNum}</Counter> : <Number>{num}</Number>}
          <Overlay />
        </Container>
      </ThemeProvider>
    </>
  )
}

export default KegelTraining
