// @flow

import styled, {createGlobalStyle} from 'styled-components'

import type {ComponentType} from 'react'

export const GlobalStyle = createGlobalStyle`
html, body, #___gatsby, #gatsby-focus-wrapper {
    height: 100%;
}

body {
    margin: 0px;
}
`

export const Container: ComponentType<{}> = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`

export const Number: ComponentType<{}> = styled.div`
  font-size: 180px;
  font-weight: bold;

  @media screen and (min-width: ${props => props.theme.spacing(125)}px) {
    font-size: 300px;
  }
`

export const Counter: ComponentType<{}> = styled(Number)`
  color: red;
`

export const Overlay: ComponentType<{}> = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
`
